import React, {Component} from 'react';

import L from 'leaflet';

import { Map, TileLayer, Marker, Popup } from 'react-leaflet'



var myIcon = L.icon({
  iconUrl: 'data:image/png;base64,',
  iconSize: [25, 41],
  iconAnchor: [12.5, 41],
  popupAnchor: [0, -41],

});

class  Location extends Component {

  state = {
  location:{  
    lat: 51.505, 
    lng: -0.09,
  },
  haveUserslocation: false,
    zoom: 2,
  }

componentDidMount(){
   navigator.geolocation.getCurrentPosition((position) =>{
    this.setState({
    location:{ 
      lat: position.coords.latitude,
      lng: position.coords.longitude
    },
    haveUserslocation:true,
    zoom: 13
    });
    
    
   }, () => {
     console.log('uh oh they did not give us their location');
     fetch('https://ipapi.co/json').then(res=> res.json()).then(location =>{
      this.setState({
        location:{ 
          lat: location.latitude,
          lng: location.longitude
        },
        haveUserslocation:true,
        zoom: 13
        });
     });

   });
}


  render() {
    const position = [this.state.location.lat, this.state.location.lng]
    return (
      <Map className= "map"  style={{height: "100%"}}  center={position} zoom={this.state.zoom}>
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />

        {this.state.haveUserslocation ? 
        
        <Marker position={position} icon={myIcon} >
          <Popup>
           Locationof the incidence <br /> Date
          </Popup>
        </Marker>: ''
      
      }
        
      </Map>
    );
  }
}

export default Location;
